<template>
  <div>
    <v-app-bar dense dark>
      <v-toolbar-title>User Adaptive Kitchen</v-toolbar-title>

      <v-toolbar-items>
        <v-btn @click="getPrediction" class="ml-4" color="blue"
          >Get Prediction</v-btn
        >
      </v-toolbar-items>

      <v-card-title>{{ prediction }} {{ confidence }}</v-card-title>
    </v-app-bar>

    <CookingRecipe />
  </div>
</template>

<script>
import axios from 'axios';
import CookingRecipe from "./CookingRecipe";

export default {
  name: "App",

  components: {
    CookingRecipe,
  },

  computed: {
    prediction() {
      return this.currentPrediction ? this.currentPrediction : "None";
    },
    confidence() {
      return this.currentConfidence > 0 ? `${this.currentConfidence}` : "None";
    },
  },

  methods: {
    async getPrediction() {
      axios
        .get("http://127.0.0.1:8000/predict")
        .then(res => {
          this.currentPrediction = res['classLabel'];
          this.currentConfidence = res['confidence'];
        })
        .catch((err) => console.log(err));
    },
  },

  data: () => ({
    currentPrediction: "",
    currentConfidence: -1,
  }),
};
</script>
